async function getVillaData(villaID)
        { var url
          if (villaID==0){
            url = '/api/villa/?format=json'
            //url ='https://api.github.com/search/repositories?q=stars:%3E1+language:javascript&sort=stars&order=desc&type=Repositories'
          }
          else{
            url = "/api/villa/".concat(villaID).concat("/?format=json")
          }          
            
        console.log('villas: ' + url)
        let response = await fetch(url, {
            method: 'GET',            
            headers: {Accept: 'application/json', 'Content-Type': 'application/json',},
        })
        //let response = await fetch(url, {method: 'GET', mode: 'no-cors'})
        let data = await response.json()
        return data
        }

export default getVillaData;        